.footer {
  background-color: rgb(66, 69, 79);
  height: 160px;
  padding-top: 40px;
  padding-left: 110px;
}

.contact {
  & h2 {
    font-size: 18px;
    font-weight: 700;
    color: #fff;
    margin-bottom: 20px;
  }

  & li {
    margin-bottom: 15px;
    font-size: 14px;
    color: #fff;
  }

  & li:hover {
    color: rgb(255, 90, 0);
    transition: 0.4s;
  }
}
