.nav {
  display: flex;
  justify-content: space-between;
  width: 1100px;
}

.logo {
  width: 150px;
  padding-top: 15px;
}
.nav-items {
  display: flex;
  padding-top: 25px;
  & ul {
    display: flex;
    padding-top: 10px;

  }
  & li {
    margin-right: 20px;
    color: rgb(66, 69, 79);
  }

  & li::after {
    display: inline-block;
    content: "";
    width: 0.5px;
    height: 13px;
    border: 1px solid rgb(66, 69, 79);
    margin-left: 15px;
  }

  & li:hover {
    color: rgb(255, 90, 0);
  }
}

.btn {
  background-color: rgb(255, 90, 0);
  text-align: center;
  padding-top: 10px;
  height: 40px;
  width: 140px;
  color: #fff;
}

.navigation {
  width: 575px;
  margin-left: auto;
  & ul {
    display: flex;
    justify-content: space-between;
  }

  & a {
    float: left;
    font-size: 16px;
    color: black;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
  }
}

.navbar {
  overflow: hidden;
  text-align: center;
}
.navbarrr {
  overflow: hidden;
  text-align: center;
}

.navbar a {
  float: left;
  font-size: 16px;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.dropdown {
  float: left;
  overflow: hidden;
}

.dropdownn {
  float: left;
  overflow: hidden;
}

.dropdown .dropbtn {
  font-size: 16px;
  border: none;
  outline: none;

  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.dropdownn .dropbtnn {
  font-size: 16px;
  border: none;
  outline: none;

  padding: 14px 16px;
  background-color: inherit;
  font-family: inherit;
  margin: 0;
}

.navbar a:hover,
.dropdown:hover .dropbtn {
  background-color: rgb(244, 242, 242);
}

.navbarrr:hover {
  color: rgb(255, 90, 0);
  .dropdown-contentss {
    display: block;
  }
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: rgb(244, 242, 242);
  min-width: 200px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.dropdown-contents {
  display: none;
  position: absolute;
  background-color: #fff;
  min-width: 500px;
  height: 300px;
  left: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}
.dropdown-contentss {
  display: none;
  position: absolute;
  background-color: rgb(244, 242, 242);
  min-width: 700px;
  height: 300px;
  top: 0px;
  left: 260px;
  // box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
  & img {
    width: 130px;
    height: 100px;
    margin-top: 75px;
  }
}

.menu {
  display: flex;
  padding-left: 70px;
  & div {
    width: 200px;
    padding-left: 70px;
    & a {
      display: block;
      padding: 0;
      margin-top: 60px;
      font-size: 13px;
    }
  }
}

.dropdown-content a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-contents a {
  float: none;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-contentss a {
  float: none;
  color: black;
  padding: 12px 16px;
  padding-left: 250px;
  text-decoration: none;
  display: block;
  text-align: left;
}

.dropdown-content a:hover {
  color: rgb(255, 90, 0);
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown-contents a:hover {
  color: rgb(255, 90, 0);
}

.dropdown:hover .dropdown-contents {
  display: block;
}

.navbar,
.dropdownn:hover .dropbtnn {
  background-color: #fff;
}
