.page-one {
  padding-top: 50px;
  padding-left: 170px;
  padding-bottom: 100px;

}

.title {
  display: flex;
  width: 430px;
  margin-bottom: 75px;
  & img {
    width: 80px;
    margin-right: 20px;
  }

  & h1 {
    font-size: 52px;
    margin-bottom: 25px;
  }

  & p {
    font-size: 16px;
  }
}

.icons{
    display: flex;
    width: 432px;
    justify-content: space-between;
}

.icon {
  display: flex;
  width: 118px;

  & img {
    width: 29px;
    height: 29px;
    margin-right: 10px;
  }

  & h2 {
    font-size: 16px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  & p {
  }
}

.icon-1{
    display: flex;
    width: 150px;
    & img {
        width: 29px;
        height: 29px;
        margin-right: 10px;
      }
    
      & h2 {
        font-size: 16px;
        font-weight: 700;
        margin-bottom: 10px;
      }
      & p {
      }
}

.btn-container{
    display: flex;
    width: 540px;
    padding-top: 35px;
    margin-top: 40px;
    border-top-style: solid;
    border-top-color: rgba(66,69,79,0.2);
    border-top-width: 0.0625rem;

    & a { 
        display: flex;
        margin-right: 20px;
        border: 2px solid  rgb(255,90,0);
        padding: 10px 20px;
        text-align: center;
        line-height: 20px;
    }
}

.coh-row-inner{
    display: flex;
}