.main{
    background-image: url('./AspenPumps_land_1600x1060-min.jpg');
    box-sizing: content-box;
    height: 500px;
    background-repeat: no-repeat;
    background-size: cover;

    padding-top: 70px;
    padding-bottom: 70px;
    padding-left: 110px;
    background-position: 100%;
}


.desc{
    width: 445px;
    & img {
        width: 110px;
        margin-bottom: 30px;
    }
    & h1 {
        color: #fff;
        font-size: 52px;
        margin-bottom: 25px;
    }
    & p {
        font-size: 16px;
        color: #fff;
    }

}